<template>
  <div class="board-list">    
    <form v-on:submit.prevent="submit" class="card-body cardbody-color p-lg-5">
      <div class="mb-3">
        <input type="email" class="form-control" id="email" v-model="email" placeholder="example@example.com">
      </div>
      <div class="mb-3">
        <select class="form-control" v-model="languageCode">
          <option value="en-US">English (US)</option>
          <option value="ko-KR">Korean</option>
          <option value="ja-JP">Japanese</option>
          <option value="zh-CN">Chinese</option>
          <!-- �ٸ� ��� �߰� ���� -->
        </select>
      </div>
      <div class="mb-3">
        <input class="form-control" id="customFile" type="file" @change="handleFileUpload" accept=".mp3"/>
      </div>
      <div class="text-center"><button type="submit" class="btn btn-color px-5 mb-5 w-100">Submit</button></div>
    </form>
  </div>
</template>

<style scoped>
.btn-color {
  background-color: #226cf6;
  color: #fff; 
}

a {
  text-decoration: none;
}
</style>

<script>
export default {
  data() {
    return {
      email: "",
      filename: "",
      languageCode: "en-US",  // �⺻ ��� ����
      base64File: null
    }
  },
  methods: {
    async submit() {
      if (!this.email || !this.base64File || !this.languageCode) {
        alert('Please provide all required fields.');
        return;
      }

      const formData = new FormData();
      formData.append('email', this.email);
      formData.append('filename', this.filename);
      formData.append('languagecode', this.languageCode);
      formData.append('file', this.base64File);

      console.log('Sending data to server:', formData);  // ����� �α� �߰�

      try {
        const response = await fetch('https://xxxxxxx.execute-api.ap-northeast-2.amazonaws.com/prod/file-upload', {
          method: 'POST',
          body: formData
        });

        console.log('Fetch response:', response);  // ����� �α� �߰�
        if (!response.ok) {
          const err = await response.json();
          throw new Error(err.error || 'Upload failed');
        }

        const data = await response.json();
        console.log(data);
        alert('File and email uploaded successfully');
      } catch (error) {
        console.error('Error:', error);
        alert(`File and email upload failed: ${error.message}`);
      }
    },
    handleFileUpload(event) {
      const file = event.target.files[0];
      if (file) {
        if (file.type !== 'audio/mpeg') {
          alert('Only MP3 files are allowed');
          return;
        }
        this.filename = file.name; // ���� �̸� ����

        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
          const base64String = reader.result.split(',')[1];
          const base64Blob = new Blob([base64String], { type: 'text/plain' });
          this.base64File = new File([base64Blob], "base64.txt", { type: 'text/plain' });
        };
      }
    }
  }
}
</script>
